.header {
  height: 39rem;
  background: url("../assets/header-bg-m.jpg") no-repeat;
  background-size: 100% 100%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.header-logo {
  height: 3.8rem;
  width: 100%;
  background: url("../assets/header-logo.png") no-repeat;
  background-size: 70% 40%;
  background-position: 50% 50%;
}

.header-title {
  height: 12rem;
  width: 100%;
  background: url("../assets/slogan.png") no-repeat;
  background-size: 80% 50%;
  background-position: 50% 31%;
}
.header-button {
  height: 6.25rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.header-button .goInto {
  height: 2.5rem;
  width: 10rem;
  border-radius: 30px;
  outline: 0px;
  border: 0px;
  background-color: #e9bb5c;
  color: #502005;
  font-size: 1rem;
  font-weight: 500;
}
.header-button .button-box {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header-button .down-logo-box img {
  width: 1.1rem;
}
.header-button .down-logo-box div {
  display: flex;
  justify-content: center;
}

.header-button .down-logo-box div:last-child {
  position: relative;
  top: 0px;
  opacity: 0.6;
}
